import * as THREE from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';



// De scene
var scene = new THREE.Scene();
scene.background = new THREE.Color('white');

// Renderer die de grootte van de container gebruikt en daarin wordt geplaatst
const container = document.getElementById('canvas-container');
const renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true, powerPreference: "high-performance",  canvas: container.querySelector('canvas') });
renderer.setSize(container.clientWidth, container.clientHeight);
renderer.setPixelRatio(window.devicePixelRatio);

// Update canvas bij resize:
window.addEventListener('resize', () => {
  const width = container.clientWidth;
  const height = container.clientHeight;
  renderer.setSize(width, height);
  camera.aspect = width / height;
  camera.updateProjectionMatrix();
});


// Camera die de grootte van de container gebruikt
var camera = new THREE.PerspectiveCamera(
  70,
  container.clientWidth / container.clientHeight,
  0.1,
  1000
);
camera.position.set(1, 1, 1);
camera.lookAt(scene.position);



// OrbitControls instellen
var controls = new OrbitControls(camera, renderer.domElement);
controls.enableDamping = true; // Schakel demping in voor soepelere beweging
controls.dampingFactor = 0.25;
controls.screenSpacePanning = false;


// Verlichting toevoegen
const ambientLight = new THREE.AmbientLight(0xf6cd8b, 2.0);
const light = new THREE.DirectionalLight(0xFFFFFF, 2.0);
light.position.set(2.5, 5, 5);
light.castShadow = true;
light.shadow.mapSize.width = 2048;
light.shadow.mapSize.height = 2048;
light.shadow.camera.near = 0.5;
light.shadow.camera.far = 50;
light.shadow.camera.left = -10;
light.shadow.camera.right = 10;
light.shadow.camera.top = 10;
light.shadow.camera.bottom = -10;
renderer.shadowMap.enabled = true;
renderer.shadowMap.type = THREE.PCFSoftShadowMap; // Dit zorgt voor zachte schaduwen
scene.add(ambientLight, light);

//schaduwvlak
let ground;

// Voeg een axis helper toe
//scene.add(new THREE.AxesHelper(2));


const OSBColorMat = new THREE.MeshStandardMaterial({ color: 0x214a09 });
const KeperColorMat = new THREE.MeshStandardMaterial({ color: 0x8e6103 });
const LatColorMat = new THREE.MeshStandardMaterial({ color: 0x6d4c08 });

const edgeMaterial = new THREE.LineBasicMaterial({ color: 0x000000 });



//-------------Functie om een object met randen te maken--------------
function createBoxWithEdges(geometry, material) {
    const mesh = new THREE.Mesh(geometry, material);
    mesh.castShadow = true;
    const edges = new THREE.EdgesGeometry(geometry);
    const line = new THREE.LineSegments(edges, edgeMaterial);
    mesh.add(line);
    return mesh;
}


//--------onderdelen------------
let Kepers = [];
let BodemPlanken =[];
let TussenPlanken =[];
let Staanders=[];
let DekselPlanken=[];


//----Variabelen-----
let Lengte;
let Breedte;
let Hoogte;
let PlaatDikte = 18/1000;
let PlankDikte = 20/1000;
let PlankBreedte = 75/1000;
let KeperBreedte = 75/1000;
let KeperHoogte = 100/1000;
let KeperKopAfstand=0/1000;

let KeperAantal;
let AantalOnderLatten;
let KeperAfstand;

let MaxSpacing=900/1000;
let TussenAfstand;

let KeperZpos;
let MaxBreedte;


//***************Variabelen inlezen en begrenzen******************

function VariabelenInlezen(){ 
    Lengte = parseFloat(document.getElementById('LengteInput').value)/1000;
    Breedte = parseFloat(document.getElementById('BreedteInput').value)/1000;
    Hoogte = parseFloat(document.getElementById('HoogteInput').value)/1000;
    AantalOnderLatten = parseFloat(document.getElementById('DekPlankenInput').value);
    KeperAantal = parseFloat(document.getElementById('KeperAantalInput').value);
    KeperAfstand = parseFloat(document.getElementById('KeperAfstandInput').value)/1000;

    //Het aantal onderlatten afronden naar een veelvoud van twee en terugsturen 

    AantalOnderLatten = Math.max(2,(Math.round(AantalOnderLatten/2)*2));
    document.getElementById('DekPlankenInput').value = AantalOnderLatten;


// Afronden
Lengte = Math.min(4,(Math.max(0.5,Lengte)));
Lengte = Math.round(Lengte * 1000) / 1000;

Breedte = Math.min(4,(Math.max(Breedte,AantalOnderLatten*PlankBreedte)));
Breedte = Math.round(Breedte * 1000) / 1000;

Hoogte = Math.min(4,(Math.max(0.2,Hoogte)));
Hoogte = Math.round(Hoogte * 1000) / 1000;

KeperAantal = Math.max(2,KeperAantal);
KeperAantal = Math.round(KeperAantal);
KeperAfstand = Math.round(KeperAfstand * 1000) / 1000;


// Terugschrijven naar de inputvelden
document.getElementById('LengteInput').value = Lengte * 1000;
document.getElementById('BreedteInput').value = Breedte * 1000;
document.getElementById('HoogteInput').value = Hoogte * 1000;
document.getElementById('KeperAantalInput').value = KeperAantal;
document.getElementById('KeperAfstandInput').value = KeperAfstand * 1000;



//De min en max van de keperafstand slider instellen
    
    const KeperAfstandSlider = document.getElementById('KeperAfstandSlider');
    KeperAfstandSlider.max = (Lengte)*1000;
    KeperAfstandSlider.min = (KeperAantal*KeperBreedte)*1000;
// De uitvoering inlezen en de plank diktes bepalen.

        if (document.getElementById('Uitvoering').value== 'Light'){
        PlankDikte=15/1000;
        PlankBreedte=70/1000;
        KeperBreedte=75/1000;
        }
    else if(document.getElementById('Uitvoering').value == 'Medium'){
        PlankDikte=18/1000;
        PlankBreedte=100/1000;
        KeperBreedte=100/1000;
        }
    else if(document.getElementById('Uitvoering').value == 'Heavy'){
        PlankDikte=22/1000;
        PlankBreedte=100/1000;
        KeperBreedte=100/1000;
        }  


//********************************KEPER INSTELLINGEN */
     if (document.getElementById('KeperAfstandCheck').checked){
        const KeperAantalInput = document.getElementById('KeperAantalInput');
        KeperAantal = KeperAantalInput.value;
        KeperAantal = Math.max(2,KeperAantal);
        KeperAantalInput.value= KeperAantal;

        KeperAfstand = Math.max(KeperAfstand,KeperAantal*KeperBreedte);
        KeperAfstand = Math.min(KeperAfstand,Lengte);
        document.getElementById('KeperAfstandInput').value=(KeperAfstand*1000).toFixed(0);

        KeperZpos = 1/2*(KeperAfstand-KeperBreedte);
        TussenAfstand = (KeperAfstand-KeperBreedte)/((KeperAantal-1)) ;

     }  
     else{
        KeperAantal = Math.max(2, Math.ceil((Lengte-2*KeperKopAfstand)/(MaxSpacing)));
        TussenAfstand = (Lengte-2*KeperKopAfstand-KeperBreedte)/(KeperAantal-1);
        KeperZpos = Lengte/2-KeperBreedte/2-KeperKopAfstand;

     } 


}


//***************oude onderdelenen verwijderen******************

function OnderdelenDeleten(){
    Kepers.forEach(a => scene.remove(a));
    BodemPlanken.forEach(a => scene.remove(a));
    TussenPlanken.forEach(a =>scene.remove(a));
    Staanders.forEach(a =>scene.remove(a));
    DekselPlanken.forEach(a=>scene.remove(a));
}



//*************BodemPlanken***************************************

function BodemPlanken_Gen(){

    for(let i=0; i<AantalOnderLatten;i++){
    let BodemPlank = createBoxWithEdges(new THREE.BoxGeometry(PlankBreedte,PlankDikte,Lengte),KeperColorMat);

        if(i<AantalOnderLatten/2){
            BodemPlank.position.set((Breedte/2-(PlankBreedte*(1+2*i))/2),PlankDikte/2,0);
        }
        else{
            BodemPlank.position.set(-1/2*(Breedte-(PlankBreedte*(1+2*(AantalOnderLatten-i-1)))),PlankDikte/2,0);
        }

    scene.add(BodemPlank);
    BodemPlanken.push(BodemPlank); 
    }
}



//*************Kepers***************************************
function Keper_Gen(){
    
    
    for (let i=0; i<KeperAantal;i++){
        
        // Afmetingen van het onderdeel (x,y,z)
      let keper = createBoxWithEdges(new THREE.BoxGeometry(Breedte, KeperHoogte,KeperBreedte), KeperColorMat);
  
      // Positioneren onder de kist
      let KeperXpos=0;
      let KeperYPos=-KeperHoogte/2;
      keper.position.set(KeperXpos, KeperYPos, KeperZpos-i*TussenAfstand);
  
      // Toevoegen aan de scene en de array
      scene.add(keper);
      Kepers.push(keper);
    }
  
}


//*************TussenPlaat***************************************
function TussenPlaat_Gen(){
    
    
    for (let i=0; i<KeperAantal;i++){

        
        // Afmetingen van het onderdeel (x,y,z)
      let TussenPlank = createBoxWithEdges(new THREE.BoxGeometry(Breedte-AantalOnderLatten*PlankBreedte, PlankDikte,PlankBreedte), KeperColorMat);
  
      // Positioneren onder de kist
      let Xpos = 0;
      let Ypos =PlankDikte/2;
      
      TussenPlank.position.set(0, Ypos, KeperZpos-i*TussenAfstand);
  
      // Toevoegen aan de scene en de array
      scene.add(TussenPlank);
      TussenPlanken.push(TussenPlank);
    }
  
}

//*************Staanders***************************************
function Staanders_Gen(){
    
    
    for (let i=0; i<KeperAantal;i++){

        
        // Afmetingen van het onderdeel (x,y,z)
      let Staander = createBoxWithEdges(new THREE.BoxGeometry(PlankDikte, Hoogte+PlankDikte+KeperHoogte,PlankBreedte), KeperColorMat);
  
      // Positioneren onder de kist
      let Xpos = 1/2*(Breedte+PlankDikte);
      let Ypos = -KeperHoogte+(Hoogte+PlankDikte+KeperHoogte)/2;
      Staander.position.set(Xpos, Ypos, KeperZpos-i*TussenAfstand);
  
      // Toevoegen aan de scene en de array
      scene.add(Staander);
      Staanders.push(Staander);
    }
    for (let a=0;a<KeperAantal;a++){
                
        // Afmetingen van het onderdeel (x,y,z)
      let Staander = createBoxWithEdges(new THREE.BoxGeometry(PlankDikte, Hoogte+PlankDikte+KeperHoogte,PlankBreedte), KeperColorMat);
  
      // Positioneren onder de kist
      let Xpos = 1/2*(Breedte+PlankDikte);
      let Ypos = -KeperHoogte+(Hoogte+PlankDikte+KeperHoogte)/2;
      Staander.position.set(-Xpos, Ypos, KeperZpos-a*TussenAfstand);
  
      // Toevoegen aan de scene en de array
      scene.add(Staander);
      Staanders.push(Staander);
    }
  
}

//*************DekselPlank***************************************
function DekselPlanken_Gen(){
    
    
    for (let i=0; i<KeperAantal;i++){

        
        // Afmetingen van het onderdeel (x,y,z)
      let DekselPlank = createBoxWithEdges(new THREE.BoxGeometry(Breedte+2*PlankDikte, PlankDikte,PlankBreedte), KeperColorMat);
  
      // Positioneren onder de kist
      let Ypos = 3/2*PlankDikte+Hoogte;
      DekselPlank.position.set(0, Ypos, KeperZpos-i*TussenAfstand);
  
      // Toevoegen aan de scene en de array
      scene.add(DekselPlank);
      DekselPlanken.push(DekselPlank);
    
      if (document.getElementById('DekselPlankenCheck').checked){
        DekselPlank.visible = true;
        }
    else {
            DekselPlank.visible =false;
        }
}
}








function UpdateKist(){
VariabelenInlezen();
OnderdelenDeleten();

    Keper_Gen();
    BodemPlanken_Gen();
    TussenPlaat_Gen();
    Staanders_Gen();
    DekselPlanken_Gen();
}








//---------animatie starten------------
function animate() {
    requestAnimationFrame(animate);
    controls.update();
    renderer.render(scene, camera);
  }
  animate();

  //-------------kist updaten bij aanpassing-----------
function setupEventListeners() {
  const elements = document.querySelectorAll("input, select, textarea");

  elements.forEach((el) => {
    if (el.tagName === "INPUT") {
      if (el.type === "range") {
        // Sliders: live update bij input
        el.addEventListener("input", UpdateKist);
      } else if (el.type === "text" || el.type === "number") {
        // Tekst/nummer: update bij blur of Enter
        el.addEventListener("blur", UpdateKist);
        el.addEventListener("keydown", (e) => {
          if (e.key === "Enter") UpdateKist();
        });
      } else if (el.type === "checkbox") {
        // Checkbox: update bij change
        el.addEventListener("change", UpdateKist);
      }
    } else if (el.tagName === "SELECT") {
      // Select: update bij change
      el.addEventListener("change", UpdateKist);
    }
  });
}
  // Start eventlisteners
  setupEventListeners();
  
  // Roep UpdateKist één keer aan om de initiële staat van de kist te tekenen
  
UpdateKist();
  
  